import React from 'react';
import "./css/offer.css";
import Footer from "./footer";
import booklogo from "./images/Booking_1.png";
import Ganash1 from './images/Ganesha1.png';
import Ganash2 from './images/Ganesha2.png';

const offer = () =>{
     return(
          <div>
          <div className="blog-banner">
          <div className="contact-img">
            
          </div> 
          <h1 className="text-gold">Contact us</h1>
          <div className="ganesh">
                    <img src={Ganash1}/>
                    <img src={Ganash2}/>
               </div>
          </div>

          <section id="booking">
               <div className="container ">

                    <div className="booking-card booking-bg1 m-auto">
                         <div className="booking-bg ">
                         <h1>Booking detail</h1>
                         <form>
                         <label>Name</label>
                         <div class="mb-3">
                              <input type="text" class="form-control" id="Name" />
                         </div>
                         <label>Email</label>
                         <div class="mb-3">
                              <input type="Email" class="form-control"  id="Email"/>
                         </div> 
                         <label>Place</label>
                         <div class="mb-3">
                              <input type="text" class="form-control"  id="date"/>
                         </div> 
                         <label>Start Date</label>
                         <div class="mb-3">
                              <input type="Date" class="form-control"  id="date"/>
                         </div>   
                         <label>End Date</label>
                         <div class="mb-3">
                              <input type="Date" class="form-control"  id="date"/>
                         </div>  
                         <div class="text-center card-button">
                              <button type="submit " className="btn ">Submit</button>
                         </div>  
                    </form>
                    </div>
                    </div>
               </div>
              
          </section>

         


          <p></p>
          

        <Footer/>
          </div>
        
     )
}

export default offer;